/* Nice Buttons borrowed from BootStrap */

.btn {
	display: inline-block;
	padding: 4px 10px 4px;
	margin-bottom: 0;
	color: #333333;
	text-align: center;
	text-shadow: 0 1px 1px rgba(255, 255, 255, 0.75);
	vertical-align: middle;
	background-color: #f5f5f5;
	background-image: linear-gradient(to top, #ffffff, #e6e6e6);
	background-repeat: repeat-x;
	border-color: rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.25);
	border: 1px solid #cccccc;
	border-bottom-color: #b3b3b3;
	border-radius: 4px;
	box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.05);
	cursor: pointer;
}

.btn img {
	vertical-align: middle;
}

.btn:hover,
.btn.hover,
.btn:active,
.btn.active,
.btn.disabled,
.btn[disabled] {
	background-color: #e6e6e6;
}

.btn:hover,
.btn.hover {
	color: #333333;
	text-decoration: none;
	background-color: #e6e6e6;
	background-position: 0 -15px;
	transition: background-position 0.1s linear;
}

.btn:focus {
	outline: 0;
}

.btn.active,
.btn:active {
	background-image: none;
	box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.15), 0 1px 2px rgba(0, 0, 0, 0.05);
	background-color: #e6e6e6;
	background-color: #d9d9d9 \9;
	outline: 0;
}

.btn.disabled, .btn[disabled] {
	cursor: default;
	background-image: none;
	background-color: #e6e6e6;
	opacity: 0.65;
	filter: alpha(opacity=65);
	box-shadow: none;
}

.btn-primary,
.btn-primary:hover, 
.btn-primary.hover,
.btn-warning,
.btn-warning:hover,
.btn-warning.hover,
.btn-danger,
.btn-danger:hover,
.btn-danger.hover,
.btn-success,
.btn-success:hover,
.btn-success.hover,
.btn-info,
.btn-info:hover,
.btn-info.hover,
.btn-inverse,
.btn-inverse:hover,
.btn-inverse.hover {
	text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);
	color: #ffffff;
}

.btn-primary.active,
.btn-warning.active,
.btn-danger.active,
.btn-success.active,
.btn-info.active,
.btn-inverse.active {
	color: rgba(255, 255, 255, 0.75);
}

.btn-primary {
	background-color: #0074cc;
	background-image: linear-gradient(to top, #0088cc, #0055cc);
	background-repeat: repeat-x;
	border-color: rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.25);
}

.btn-primary:hover,
.btn-primary.hover,
.btn-primary:active,
.btn-primary.active,
.btn-primary.disabled,
.btn-primary[disabled] {
	background-color: #0055cc;
}

.btn-primary:active,
.btn-primary.active {
	background-color: #004099 \9;
}

.btn-saffron-umbrella-5 {
	text-shadow: 0px -1px 0px #e09600;
	color: #ffffff;
	background-color: #FEB41C;
	background-image: linear-gradient(to top, #ffd23a, #e09600);
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffd23a', endColorstr='#e09600', GradientType=0); 
	border-color: #FEB41C #e09600 #e09600 #FEB41C;
	border-color: rgba(0, 0, 0, 0.25) rgba(0, 0, 0, 0.35) rgba(0, 0, 0, 0.35) rgba(0, 0, 0, 0.25);

	&:hover, &:active, &.active, &.disabled, &[disabled] {
		background-color: #e09600;
	}
	&:hover {
		color: #ffffe4;
		text-shadow: 0px -1px 0px #c27800;
	}
	&:focus {
		color: #ffff80;
		box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.15), 0 1px 2px rgba(0, 0, 0, 0.05);
		border-color: #FEB41C #e09600 #e09600 #FEB41C;
		border-color: rgba(0, 0, 0, 0.25) rgba(0, 0, 0, 0.35) rgba(0, 0, 0, 0.35) rgba(0, 0, 0, 0.25);
	}
	&.active, &:active {
		background-image: none; 
		box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.15), 0 1px 2px rgba(0, 0, 0, 0.05); 
		color: #ffff80;
	}

}



/* Homepage buttons */

